* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 5px;
}

:root {
  --primary: #b22234;
  --secondary: #3c3b6e;
  --primaryDark: #000;
  --primaryLight: #fff;
  --gray: rgba(201, 48, 48, 0.199);
  --grey: rgba(128, 128, 128, 0.199);
}

.container {
  max-width: 1650px;
  min-height: calc(100vh - (40vh + 136px));
  margin: 0 auto;
  padding-bottom: 4vh;
}
.active {
  border-bottom: 2px solid var(--primary);
  border-radius: 0px;
}
/* media query to have border on .active class only width of txt on small screens */
@media (max-width: 768px) {
  .headertext {
    width: auto;
  }
  .active {
    border-bottom: 2px solid var(--primary);
    border-radius: 0px;
  }
}

.title {
  color: var(--primaryLight) !important;
  text-shadow: 3px 3px 2px var(--primary) !important;
  font-weight: 550 !important;
  font-style: normal !important;
}

.blurb {
  color: var(--primary) !important;
  line-height: 35px !important;
  font-weight: 400 !important;
  font-style: normal !important;
  text-align: center !important;
}

.btn {
  color: white !important;
  background: #b22234 !important;
  border-radius: 5px !important;
  box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.2) !important;
}

.btn1 {
  color: white !important;
  background: #3c3b6e !important;
  border-radius: 5px !important;
  box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.2) !important;
  width: 100% !important;
  padding: .3em;
  font-size: 0.875rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  border:none;
}

.btn:hover {
  background-color: #9c1c2e !important;
}

.btn1:hover {
  background-color: #2c2c52 !important;
}

.lgBtn {
  padding: 2% 4% !important;
  width: 80% !important;
  margin: 1em 0 !important;
}

.cardIcon {
  font-size: 120px !important;
}

.input {
  background: var(--primaryLight) !important;
  border-radius: 5px !important;
  outline: none !important;
  box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.2);
}

.atag {
  text-decoration: none;
}

.imgdiv1 {
  display: flex;
  justify-content: center;
