  color: var(--primaryLight);
  width: 30px;
  height: 30px;
  border: none;
  margin: 10px;
  box-shadow: 4px 4px 4px 1px rgba(0, 0, 0, 0.2) !important;
  font-size: large;
}

.lbtn {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.rbtn {
